import Vue from 'vue'
import axios from '@/axios';
import Vant from 'vant';
import 'vant/lib/index.css';
import vueCookies from 'vue-cookies'
import Watch from './Watch.vue'


Vue.use(vueCookies);
Vue.use(Vant);

new Vue({
  render: h => h(Watch),
	axios
}).$mount('#app')



